<template lang='pug'>
  .brew-ring-image-block(:class="type")
    image-block(
    :title="title"
    :showButton="true"
    :supportMask="false"
    @click="$router.push(routerPath)"
    :maskDescription="maskDescription")
</template>
<script>
import imageBlock from '@/components/ImageBlock.vue';

export default {
  name: 'brew-ring-image-block',
  props: {
    title: {
      type: String,
    },
    routerPath: {
      type: String,
    },
    maskDescription: {
      type: String,
    },
  },
  components: {
    imageBlock,
  },
  computed: {
    type() {
      const splitResult = this.routerPath.split('/');
      return `brew-ring-image-block--${splitResult[splitResult.length - 1]}`;
    },
  },
};
</script>

<style lang="scss" scoped>
@import '../scss/_mediaQuery.scss';
@import '../scss/_variable.scss';

  @mixin ring-background($ring-type) {
    ::v-deep .image-block__background {
      background-image:
        url('~assets/marriageBrewRing/series-' + $ring-type + '@2x.jpg') !important;
      @include tablet-and-mobile-only {
        background-image:
        url('~assets/marriageBrewRing/series-' + $ring-type + '-m@2x.jpg') !important;
      }
    }
  }

.brew-ring-image-block {
  &--scratch {
    @include ring-background('scratch');
  }
  &--lava {
    @include ring-background('lava');
  }
  &--engraving {
    @include ring-background('engraving');
  }
  &--tassel {
    @include ring-background('tassel');
  }
}

</style>
